* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #ceeaf2;

  /* height: 100vh; */
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.line {
  position: relative;
  margin: 20px auto;
  width: 95%;
  height: 80vh;
  padding: 40px;

  border-radius: 10px;
  background: #ceeaf2;
box-shadow: inset 10px 10px 20px #adc5cb,
            inset -10px -10px 20px #efffff;

}

.type {
  width: max-content;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 50px;
  padding: 15px 40px;
  /* border: red 1px groove; */

  border-radius: 10px;
  background: #ceeaf2;
  box-shadow: inset 10px 10px 20px #adc5cb,
              inset -10px -10px 20px #efffff;


}

.up {
  padding: 10px;
  border-radius: 10px;
  background: #ceeaf2;
  box-shadow:  10px 10px 20px #adc5cb,
             -10px -10px 20px #efffff;
}

.val {
  width: max-content;
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;

  border-radius: 10px;
  background: #ceeaf2;
box-shadow: inset 10px 10px 20px #adc5cb,
            inset -10px -10px 20px #efffff;
}


input[type=number] {
  border: none;
  padding: 10px;
  margin: 10px;
  text-align: center;

  border-radius: 10px;
  background: #ceeaf2;
  box-shadow:  10px 10px 20px #adc5cb,
             -10px -10px 20px #efffff;
}

input[type=number]:focus, input[type=number]:hover {
  outline: 3px #efffff solid !important;
}

button {
  border: none;
  border-radius: 8px;
  margin: 5px;

  width: 40px;
  height: 40px;

  background: #ceeaf2;
  box-shadow:  10px 10px 20px #adc5cb,
             -10px -10px 20px #efffff;
}

button:hover {
  border: #efffff 3px solid;
}


input[type=number] {
  -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;}


.question {
  position: absolute;
  top: 10px;
  left: 10px;
}


.flex {
  margin: 45px;
  margin-bottom: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;

}

.info {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* background-color: rgba(187, 247, 255, 0.8); */

  display: grid;
  align-items: center;
  justify-content: center;
  
}

.back {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(187, 247, 255, 0.8);

  display: grid;
  align-items: center;
  justify-content: center;
}

.info_wid {
  width: max(30vw, 400px);
  height: max-content;
  background-color: white;
  border-radius: 20px;

  position: relative;
}

.close {
  position: absolute;
  right: 20px;
  top: 20px;
}

.link {
  color: #886CE4;
  white-space: nowrap;
}

